import './loaders.css'
import React from 'react'
import plato from './plato-3069619_640.png'

export const PlatoSpinner = (props) => (
  <div className='loading'>
    <div className='loading__logo'>
      <img alt='progress spinner' src={plato} />
    </div>
  </div>
)

export default PlatoSpinner



